
/* Flashlight Overlay */
#darkFun {
    cursor: none;
    --cursorX: 50vw;
    --cursorY: 50vh;
}
#darkFun:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: fixed;
    pointer-events: none;
    background: radial-gradient(
            circle 20vmax at var(--cursorX) var(--cursorY),
            rgba(0,0,0,0) 0%,
            rgba(0,0,0,.5) 80%,
            rgba(0,0,0,.95) 100%
    )
}